import React, { useState, useEffect } from "react";
import ReactMarkdown from "react-markdown";

export default function RepoReadme({ login, name }) {
  const URL = `https://api.github.com/repos/${login}/${name}/readme`;

  const [markdown, setMarkdown] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  async function loadMD(url) {
    try {
      setMarkdown(null);
      setLoading(true);
      setError(null);
      const res = await fetch(url);
      const mdUrl = await res.json();
      if (!mdUrl.download_url) {
        setError(mdUrl);
      }
      const mdRes = await fetch(mdUrl.download_url);
      const md = await mdRes.text();
      setMarkdown(md);
      setLoading(false);
    } catch (e) {
      setError(e);
    }
  }

  useEffect(() => {
    if (!login || !name) return;
    loadMD(URL);
  }, [login, name]);

  if (loading) return <h2>Loading readme ...</h2>;
  if (error) return <pre>{JSON.stringify(error, null, 2)}</pre>;

  return <ReactMarkdown children={markdown} className="markdown" />;
  //   return <>{markdown}</>;
}
