import React from "react";
import useIterator from "./useIterator.js";
import RepoReadme from "./RepoReadme.js";

export default function RepoMenu({ repositories, login }) {
  let repoArray = Object.values(repositories);
  repoArray.splice(-1, 1);
  const [repo, prev, next] = useIterator(repoArray);

  if (!repo) return;

  return (
    <div style={{ display: "flex" }}>
      <div className="toggle-names">
        <button className="repoButton" onClick={prev}>
          &lt;
        </button>
        <a
          className="repoName"
          href={repo.clone_url}
          target="_blank"
          rel="noreferrer"
        >
          {repo.name}
        </a>
        <button className="repoButton" onClick={next}>
          &gt;
        </button>
      </div>
      <RepoReadme login={login} name={repo.name} />
    </div>
  );
}
