import React from "react";
import RepoList from "./RepoList.js";
import Fetch from "./Fetch.js";

export default function UserInfo({ login }) {
  const USR_URL = "https://api.github.com/users/" + login;

  function userDetails(data) {
    return (
      <div className="githubUser">
        <img src={data.avatar_url} alt={data.login} style={{ width: 200 }} />
        <div>
          <h1>{data.login}</h1>
          {data.name && <p>{data.name}</p>}
          {data.location && <p>{data.location}</p>}
          {data.created_at && <p>{data.created_at}</p>}
          {data.html_url && (
            <a href={data.html_url} target="_blank" rel="noreferrer">
              {data.html_url}
            </a>
          )}
        </div>
        <RepoList login={data.login} className="repo-list"/>
      </div>
    );
  }

  return <Fetch uri={USR_URL} renderSuccess={userDetails} />;
}
