import React from "react";
import Fetch from "./Fetch.js";
import RepoMenu from "./RepoMenu.js";

export default function RepoList({ login }) {
  const REPO_URL = `https://api.github.com/users/${login}/repos`;

  return (
    <Fetch
      uri={REPO_URL}
      renderSuccess={(data) => <RepoMenu repositories={data} login={login}/>}
    />
  );
}
