import { useState, useEffect } from "react";

export default function useIterator(items = [], initialValue = 0) {
  const [name, setName] = useState(items[initialValue]);
  const [index, setIndex] = useState(initialValue);

  useEffect(() => {
    setName(items[index]);
  }, [index]);

  const prev = () => {
    if (index === 0) {
      setIndex(items.length - 1);
    } else {
      setIndex(index - 1);
    }
  };

  const next = () => {
    if (index === items.length - 1) {
      setIndex(0);
    } else {
      setIndex(index + 1);
    }
  };

  return [name, prev, next];
}
